import React from "react";
import { Link } from "react-router-dom";
import CheckCircleOutlineTwoToneIcon from "@material-ui/icons/CheckCircleOutlineTwoTone";

import PrimaryButton from "../../elements/PrimaryButton";

const ErrorNewProject = ({ resetCreateProject }) => {
  return (
    <div className="ErrorNewProject flex flex-col items-center">
      <CheckCircleOutlineTwoToneIcon
        className="text-red-400"
        style={{ fontSize: 100 }}
      />
      <p className="text-xl text-red-500 pt-4 mb-24">
        Cannot create project. Please try again.
      </p>

      <Link to="/projects" onClick={resetCreateProject}>
        <PrimaryButton className="w-full bg-primary-2">
          Goto Project Dashboard
        </PrimaryButton>
      </Link>
    </div>
  );
};

export default ErrorNewProject;
