import axios from "utils/axiosInstance.util";
import { getUserFromLocalStorage } from "utils/auth.util";

export default async (projectId) => {
  let token = getUserFromLocalStorage();

  let url = `/api/${process.env.REACT_APP_API_VERSION}/project/${projectId}/details`;

  let { data } = await axios.get(url, {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });

  return data;
};
