import { CREATE_PROJECT, RESET_CREATE_PROJECT } from "./types";

// Service
import createProjectService from "services/api/project/createProject.service";

export const createProject = (data) => async (dispatch) => {
  try {
    dispatch({ type: CREATE_PROJECT.TRIGGER });

    dispatch({ type: CREATE_PROJECT.REQUEST });

    let res = await createProjectService(data);

    dispatch({
      type: CREATE_PROJECT.SUCCESS,
      payload: res,
    });
  } catch (err) {
    let error = {};

    if (err.response) {
      error = err.response.data;
    } else {
      error.message = err.message ? err.message : err;
    }
    dispatch({
      type: CREATE_PROJECT.FAILURE,
      payload: error,
    });
  } finally {
    dispatch({ type: CREATE_PROJECT.FULFIL });
  }
};

export const resetCreateProject = () => (dispatch) => {
  dispatch({ type: RESET_CREATE_PROJECT });
};
