import React, { useState, useEffect } from "react";
import { connect } from "react-redux";

import PrimaryButton from "../../elements/PrimaryButton";
import ProcessingNewProject from "./ProcessingNewProject";
import SuccessNewProject from "./SuccessNewProject";
import ErrorNewProject from "./ErrorNewProject";

import {
  createProject,
  resetCreateProject,
} from "actions/project/createProject";
import { unsetCurrentProject } from "actions/project/setCurrentProject";

const CreateNewProjectPage = ({
  createProject,
  isLoading,
  success,
  error,
  resetCreateProject,
  unsetCurrentProject,
}) => {
  const [name, setName] = useState("");

  const handleChange = ({ target }) => {
    let { value } = target;

    setName(value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    createProject({ name });
  };

  useEffect(() => {
    unsetCurrentProject();
  }, []);

  return (
    <div className="CreateNewProjectPage flex">
      <div className="w-3/4 h-screen flex items-center justify-center">
        {isLoading ? (
          <ProcessingNewProject />
        ) : success ? (
          <SuccessNewProject resetCreateProject={resetCreateProject} />
        ) : error ? (
          <ErrorNewProject resetCreateProject={resetCreateProject} />
        ) : (
          <div className="w-full px-20 flex flex-col">
            <p className="text-6xl text-gray-700 mb-16">Create a Project</p>

            <p className="text-4xl text-gray-600 mb">
              Start by entering the name of your project
            </p>

            <form
              className="w-independent-form py-4 pt-8"
              onSubmit={handleSubmit}
            >
              <div className="pb-4">
                <input
                  type="text"
                  name="projectname"
                  id="projectname"
                  className="shadow appearance-none border rounded w-full py-4 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-gray-300 "
                  placeholder="Enter the Project name"
                  value={name}
                  onChange={handleChange}
                />
              </div>

              <div>
                <PrimaryButton className="w-full bg-primary-2" type="submit">
                  Create Project
                </PrimaryButton>
              </div>
            </form>
          </div>
        )}
      </div>

      <div className="w-1/4 bg-gray-500 h-screen"></div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  isLoading: state.project.createProject.isLoading,
  success: state.project.createProject.data,
  error: state.project.createProject.error,
});

const mapDispatchToProps = {
  createProject,
  resetCreateProject,
  unsetCurrentProject,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CreateNewProjectPage);
