import { SET_CURRENT_PROJECT, UNSET_CURRENT_PROJECT } from "./types";

// Service
import getProjectDetailsService from "services/api/project/getProjectDetails.service";

import {
  setCurrentProjectToLocalStorage,
  removeCurrentProjectFromLocalStorage,
} from "utils/project.util";

export const setCurrentProject = (project) => async (dispatch) => {
  try {
    dispatch({ type: SET_CURRENT_PROJECT.TRIGGER });

    dispatch({ type: SET_CURRENT_PROJECT.REQUEST });

    if (typeof project === "string") {
      let res = await getProjectDetailsService(project);
      setCurrentProjectToLocalStorage(res);

      dispatch({
        type: SET_CURRENT_PROJECT.SUCCESS,
        payload: res,
      });
    } else {
      dispatch({
        type: SET_CURRENT_PROJECT.SUCCESS,
        payload: project,
      });
    }
  } catch (err) {
    let error = {};

    if (err.response) {
      error = err.response.data;
    } else {
      error.message = err.message ? err.message : err;
    }
    dispatch({
      type: SET_CURRENT_PROJECT.FAILURE,
      payload: error,
    });
  } finally {
    dispatch({ type: SET_CURRENT_PROJECT.FULFIL });
  }
};

export const unsetCurrentProject = () => (dispatch) => {
  dispatch({ type: UNSET_CURRENT_PROJECT });
  removeCurrentProjectFromLocalStorage();
};
