import React from "react";
import Loader from "react-loader-spinner";

const ProcessingNewProject = (params) => {
  return (
    <div className="ProcessingNewProject flex flex-col items-center">
      <Loader
        type="MutatingDots"
        color="#F0806F"
        secondaryColor="#F0806F"
        height={100}
        width={100}
        timeout={0} //3 secs
      />
      <p className="text-2xl text-gray-700">Creating your project...</p>
    </div>
  );
};

export default ProcessingNewProject;
