import React from "react";
import { Link } from "react-router-dom";
import CheckCircleOutlineTwoToneIcon from "@material-ui/icons/CheckCircleOutlineTwoTone";

import PrimaryButton from "../../elements/PrimaryButton";

const SuccessNewProject = ({ resetCreateProject }) => {
  return (
    <div className="SuccessNewProject flex flex-col items-center">
      <CheckCircleOutlineTwoToneIcon
        className="text-green-400"
        style={{ fontSize: 100 }}
      />
      <p className="text-xl text-green-500 pt-4 mb-24">
        Your new project is ready
      </p>

      <Link to="/projects" onClick={resetCreateProject}>
        <PrimaryButton className="w-full bg-primary-2">
          Goto Project Dashboard
        </PrimaryButton>
      </Link>
    </div>
  );
};

export default SuccessNewProject;
